@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700&family=Roboto:wght@300;400;500;700&display=swap);
:root {
  /* Color scheme */
  --dark-bg: #121218;
  --medium-bg: #1a1a24;
  --light-bg: #252535;
  --text-light: #f0f0f0;
  --text-medium: #b0b0b0;
  --text-dark: #707070;
  --accent-color: #64ffda;
  --secondary-accent: #7c4dff;
  --ufo-glow: #00e5ff;
  --military-color: #ff4d4d;
  --nuclear-color: #ffcc00;
  --danger-color: #ff4d4d;
  --success-color: #4caf50;
  --warning-color: #ff9800;
  
  /* Shadows and effects */
  --card-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  --hover-shadow: 0 6px 25px rgba(0, 0, 0, 0.4);
  --text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
  
  /* Transitions */
  --transition-fast: 0.2s ease;
  --transition-medium: 0.3s ease;
  --transition-slow: 0.5s ease;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--dark-bg);
  color: var(--text-light);
  line-height: 1.6;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  background-color: var(--light-bg);
  padding: 2px 5px;
  border-radius: 4px;
  font-size: 0.9em;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Orbitron', sans-serif;
  font-weight: 600;
  margin-bottom: 1rem;
  color: var(--text-light);
}

h1 {
  font-size: 2.5rem;
  letter-spacing: 1px;
}

h2 {
  font-size: 2rem;
  letter-spacing: 0.8px;
}

h3 {
  font-size: 1.5rem;
  letter-spacing: 0.6px;
}

a {
  color: var(--accent-color);
  text-decoration: none;
  transition: color var(--transition-fast);
}

a:hover {
  color: var(--ufo-glow);
  text-decoration: underline;
}

button {
  font-family: 'Roboto', sans-serif;
  background-color: var(--medium-bg);
  color: var(--text-light);
  border: 1px solid var(--accent-color);
  border-radius: 4px;
  padding: 8px 16px;
  cursor: pointer;
  transition: all var(--transition-fast);
  font-size: 14px;
  outline: none;
}

button:hover {
  background-color: rgba(100, 255, 218, 0.1);
  box-shadow: 0 0 10px rgba(100, 255, 218, 0.3);
}

button:active {
  transform: translateY(1px);
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--medium-bg);
}

::-webkit-scrollbar-thumb {
  background: var(--light-bg);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--accent-color);
}

/* Utility classes */
.text-accent {
  color: var(--accent-color);
}

.text-secondary {
  color: var(--secondary-accent);
}

.text-danger {
  color: var(--danger-color);
}

.text-success {
  color: var(--success-color);
}

.text-warning {
  color: var(--warning-color);
}

.text-center {
  text-align: center;
}

.loading-spinner {
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-top: 4px solid var(--accent-color);
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  h1 {
    font-size: 2rem;
  }
  
  h2 {
    font-size: 1.6rem;
  }
  
  h3 {
    font-size: 1.3rem;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 1.8rem;
  }
  
  h2 {
    font-size: 1.4rem;
  }
  
  h3 {
    font-size: 1.2rem;
  }
  
  button {
    padding: 6px 12px;
    font-size: 13px;
  }
}

.map-visualization {
  position: relative;
  width: 100%;
  background-color: var(--medium-bg);
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.05);
  overflow: hidden;
}

.tooltip {
  position: absolute;
  padding: 12px;
  background: rgba(20, 20, 30, 0.9);
  border: 1px solid var(--accent-color);
  border-radius: 8px;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.2s;
  font-size: 14px;
  z-index: 1000;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  color: var(--text-light);
  max-width: 250px;
}

.tooltip strong {
  color: var(--accent-color);
  display: block;
  margin-bottom: 5px;
  font-family: 'Orbitron', sans-serif;
}

.state {
  transition: fill 0.3s, stroke 0.3s;
  cursor: pointer;
}

.state:hover {
  stroke: var(--accent-color);
  stroke-width: 1.5;
  filter: brightness(1.1);
}

.ufo-sighting {
  transition: r 0.3s, fill 0.3s;
  cursor: pointer;
}

.ufo-sighting:hover {
  filter: brightness(1.5);
  animation: pulse 1.5s infinite;
}

.military-base {
  transition: fill 0.3s;
  cursor: pointer;
}

.military-base:hover {
  filter: brightness(1.3);
}

.nuclear-reactor {
  transition: r 0.3s, fill 0.3s;
  cursor: pointer;
}

.nuclear-reactor:hover {
  filter: brightness(1.3);
}

.reactor-buffer {
  pointer-events: none;
  opacity: 0.3;
}

.cluster {
  transition: r 0.3s, fill 0.3s;
  cursor: pointer;
}

.cluster:hover {
  filter: brightness(1.3);
}

.zoom-controls {
  opacity: 0.8;
  transition: opacity 0.3s;
}

.zoom-controls:hover {
  opacity: 1;
}

.zoom-button {
  cursor: pointer;
  transition: all 0.2s;
}

.zoom-button:hover {
  fill: rgba(255, 255, 255, 0.95);
  stroke: var(--accent-color);
}

.legend {
  opacity: 0.9;
  transition: opacity 0.3s;
}

.legend:hover {
  opacity: 1;
}

.heatmap-layer {
  opacity: 0.85;
  mix-blend-mode: screen;
}

/* Rendering overlay */
.rendering-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 100;
  border-radius: 12px;
}

.rendering-progress {
  width: 80%;
  height: 8px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 15px;
}

.progress-bar {
  height: 100%;
  background: linear-gradient(to right, var(--accent-color), var(--ufo-glow));
  border-radius: 4px;
  transition: width 0.3s ease;
}

.rendering-text {
  color: var(--text-light);
  font-size: 16px;
  font-family: 'Orbitron', sans-serif;
}

/* Map info panel */
.map-info {
  position: absolute;
  top: 20px;
  left: 20px;
  background-color: rgba(20, 20, 30, 0.8);
  border-radius: 8px;
  padding: 10px 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: var(--text-light);
  font-size: 14px;
  z-index: 10;
}

.info-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
}

.info-item:last-child {
  margin-bottom: 0;
}

.info-label {
  color: var(--text-medium);
  margin-right: 10px;
}

.info-value {
  color: var(--accent-color);
  font-weight: 500;
}

/* Animations */
@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 0.6;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.9;
  }
  100% {
    transform: scale(1);
    opacity: 0.6;
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .map-visualization {
    padding: 15px;
  }
  
  .map-info {
    top: 15px;
    left: 15px;
    font-size: 12px;
  }
  
  .zoom-controls {
    transform: scale(0.8);
    transform-origin: top right;
  }
  
  .legend {
    transform: scale(0.8);
    transform-origin: bottom left;
  }
}
