  border-radius: 12px;
  overflow: hidden;
  box-shadow: var(--card-shadow);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.chart-block {
  margin-bottom: 30px;
}

.chart {
  flex: 2 1;
  background-color: var(--medium-bg);
  border-radius: 8px;
  padding: 20px;
  box-shadow: var(--card-shadow);
}

.chart-description {
  flex: 1 1;
  padding: 20px;
  background-color: rgba(124, 77, 255, 0.05);
  border-radius: 0 12px 12px 0;
  font-size: 14px;
  line-height: 1.6;
  border-left: 2px solid var(--secondary-accent);
  color: var(--text-medium);
}

.chart-description h3 {
  margin-top: 0;
  color: var(--accent-color);
  font-size: 1.2rem;
  margin-bottom: 15px;
}

.chart-description p {
  margin-bottom: 15px;
}

.chart-description ul {
  padding-left: 20px;
  margin-bottom: 15px;
}

.chart-description li {
  margin-bottom: 8px;
}

.data-stats {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 20px;
  gap: 20px;
  margin-bottom: 30px;
  justify-content: center;
}

.stat-card {
  background-color: var(--medium-bg);
  border-radius: 8px;
  padding: 15px;
  min-width: 180px;
  flex: 1 1;
  text-align: center;
  box-shadow: var(--card-shadow);
  border: 1px solid rgba(255, 255, 255, 0.05);
  transition: transform var(--transition-fast);
}

.stat-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--hover-shadow);
}

.stat-value {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 5px;
  color: var(--accent-color);
  font-family: 'Orbitron', sans-serif;
}

.stat-label {
  font-size: 0.9rem;
  color: var(--text-medium);
}

.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 300px;
}

.loading-text {
  margin-top: 20px;
  color: var(--text-medium);
  font-size: 1rem;
}

.error-container {
  background-color: rgba(255, 77, 77, 0.1);
  border: 1px solid var(--danger-color);
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 30px;
  color: var(--text-light);
}

.error-title {
  color: var(--danger-color);
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.error-message {
  margin-bottom: 15px;
}

.error-retry {
  background-color: var(--danger-color);
  border-color: var(--danger-color);
  color: white;
}

.error-retry:hover {
  background-color: rgba(255, 77, 77, 0.8);
}

.footer {
  margin-top: 60px;
  padding-top: 20px;
  border-top: 1px solid var(--light-bg);
  text-align: center;
  color: var(--text-dark);
  font-size: 0.9rem;
}

@media (max-width: 768px) {
  .App {
    padding: 15px;
  }
  
  .app-title {
    font-size: 2.2rem;
  }
  
  .app-subtitle {
    font-size: 1rem;
  }
  
  .chart-container {
    flex-direction: column;
  }
  
  .chart, .chart-description {
    width: 100%;
    border-radius: 8px;
  }
  
  .chart-description {
    border-left: none;
    border-top: 2px solid var(--secondary-accent);
  }
  
  .control-group {
    min-width: 100%;
  }
  
  .stat-card {
    min-width: 140px;
  }
}
