.proximity-analysis {
  background-color: var(--medium-bg);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 30px;
  box-shadow: var(--card-shadow);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.proximity-title {
  color: var(--accent-color);
  font-size: 1.2rem;
  margin-bottom: 15px;
}

.proximity-controls {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 20px;
}

.proximity-control {
  display: flex;
  flex-direction: column;
  min-width: 200px;
}

.proximity-label {
  color: var(--text-medium);
  font-size: 0.9rem;
  margin-bottom: 8px;
}

.proximity-select,
.proximity-input {
  background-color: var(--light-bg);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  padding: 8px 12px;
  color: var(--text-light);
  font-size: 14px;
  width: 100%;
}

.proximity-select:focus,
.proximity-input:focus {
  outline: none;
  border-color: var(--accent-color);
  box-shadow: 0 0 0 2px rgba(100, 255, 218, 0.2);
}

.proximity-button {
  background-color: var(--accent-color);
  color: var(--dark-bg);
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-fast);
  align-self: flex-end;
}

.proximity-button:hover {
  background-color: rgba(100, 255, 218, 0.8);
  transform: translateY(-2px);
}

.proximity-results {
  margin-top: 20px;
}

.proximity-result-item {
  background-color: var(--light-bg);
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 10px;
  border-left: 4px solid var(--accent-color);
}

.proximity-result-title {
  font-weight: 600;
  color: var(--text-light);
  margin-bottom: 8px;
}

.proximity-result-details {
  color: var(--text-medium);
  font-size: 0.9rem;
}

.proximity-result-distance {
  color: var(--accent-color);
  font-weight: 600;
}

.proximity-chart {
  margin-top: 30px;
  height: 300px;
  width: 100%;
}

.proximity-chart-title {
  color: var(--text-light);
  font-size: 1rem;
  margin-bottom: 15px;
  text-align: center;
}

@media (max-width: 768px) {
  .proximity-controls {
    flex-direction: column;
    gap: 15px;
  }
  
  .proximity-control {
    width: 100%;
  }
}
