.map-visualization {
  position: relative;
  width: 100%;
  background-color: var(--medium-bg);
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.05);
  overflow: hidden;
}

.tooltip {
  position: absolute;
  padding: 12px;
  background: rgba(20, 20, 30, 0.9);
  border: 1px solid var(--accent-color);
  border-radius: 8px;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.2s;
  font-size: 14px;
  z-index: 1000;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  color: var(--text-light);
  max-width: 250px;
}

.tooltip strong {
  color: var(--accent-color);
  display: block;
  margin-bottom: 5px;
  font-family: 'Orbitron', sans-serif;
}

.state {
  transition: fill 0.3s, stroke 0.3s;
  cursor: pointer;
}

.state:hover {
  stroke: var(--accent-color);
  stroke-width: 1.5;
  filter: brightness(1.1);
}

.ufo-sighting {
  transition: r 0.3s, fill 0.3s;
  cursor: pointer;
}

.ufo-sighting:hover {
  filter: brightness(1.5);
  animation: pulse 1.5s infinite;
}

.military-base {
  transition: fill 0.3s;
  cursor: pointer;
}

.military-base:hover {
  filter: brightness(1.3);
}

.nuclear-reactor {
  transition: r 0.3s, fill 0.3s;
  cursor: pointer;
}

.nuclear-reactor:hover {
  filter: brightness(1.3);
}

.reactor-buffer {
  pointer-events: none;
  opacity: 0.3;
}

.cluster {
  transition: r 0.3s, fill 0.3s;
  cursor: pointer;
}

.cluster:hover {
  filter: brightness(1.3);
}

.zoom-controls {
  opacity: 0.8;
  transition: opacity 0.3s;
}

.zoom-controls:hover {
  opacity: 1;
}

.zoom-button {
  cursor: pointer;
  transition: all 0.2s;
}

.zoom-button:hover {
  fill: rgba(255, 255, 255, 0.95);
  stroke: var(--accent-color);
}

.legend {
  opacity: 0.9;
  transition: opacity 0.3s;
}

.legend:hover {
  opacity: 1;
}

.heatmap-layer {
  opacity: 0.85;
  mix-blend-mode: screen;
}

/* Rendering overlay */
.rendering-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 100;
  border-radius: 12px;
}

.rendering-progress {
  width: 80%;
  height: 8px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 15px;
}

.progress-bar {
  height: 100%;
  background: linear-gradient(to right, var(--accent-color), var(--ufo-glow));
  border-radius: 4px;
  transition: width 0.3s ease;
}

.rendering-text {
  color: var(--text-light);
  font-size: 16px;
  font-family: 'Orbitron', sans-serif;
}

/* Map info panel */
.map-info {
  position: absolute;
  top: 20px;
  left: 20px;
  background-color: rgba(20, 20, 30, 0.8);
  border-radius: 8px;
  padding: 10px 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: var(--text-light);
  font-size: 14px;
  z-index: 10;
}

.info-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
}

.info-item:last-child {
  margin-bottom: 0;
}

.info-label {
  color: var(--text-medium);
  margin-right: 10px;
}

.info-value {
  color: var(--accent-color);
  font-weight: 500;
}

/* Animations */
@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 0.6;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.9;
  }
  100% {
    transform: scale(1);
    opacity: 0.6;
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .map-visualization {
    padding: 15px;
  }
  
  .map-info {
    top: 15px;
    left: 15px;
    font-size: 12px;
  }
  
  .zoom-controls {
    transform: scale(0.8);
    transform-origin: top right;
  }
  
  .legend {
    transform: scale(0.8);
    transform-origin: bottom left;
  }
}