@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700&family=Roboto:wght@300;400;500;700&display=swap');

:root {
  /* Color scheme */
  --dark-bg: #121218;
  --medium-bg: #1a1a24;
  --light-bg: #252535;
  --text-light: #f0f0f0;
  --text-medium: #b0b0b0;
  --text-dark: #707070;
  --accent-color: #64ffda;
  --secondary-accent: #7c4dff;
  --ufo-glow: #00e5ff;
  --military-color: #ff4d4d;
  --nuclear-color: #ffcc00;
  --danger-color: #ff4d4d;
  --success-color: #4caf50;
  --warning-color: #ff9800;
  
  /* Shadows and effects */
  --card-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  --hover-shadow: 0 6px 25px rgba(0, 0, 0, 0.4);
  --text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
  
  /* Transitions */
  --transition-fast: 0.2s ease;
  --transition-medium: 0.3s ease;
  --transition-slow: 0.5s ease;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--dark-bg);
  color: var(--text-light);
  line-height: 1.6;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  background-color: var(--light-bg);
  padding: 2px 5px;
  border-radius: 4px;
  font-size: 0.9em;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Orbitron', sans-serif;
  font-weight: 600;
  margin-bottom: 1rem;
  color: var(--text-light);
}

h1 {
  font-size: 2.5rem;
  letter-spacing: 1px;
}

h2 {
  font-size: 2rem;
  letter-spacing: 0.8px;
}

h3 {
  font-size: 1.5rem;
  letter-spacing: 0.6px;
}

a {
  color: var(--accent-color);
  text-decoration: none;
  transition: color var(--transition-fast);
}

a:hover {
  color: var(--ufo-glow);
  text-decoration: underline;
}

button {
  font-family: 'Roboto', sans-serif;
  background-color: var(--medium-bg);
  color: var(--text-light);
  border: 1px solid var(--accent-color);
  border-radius: 4px;
  padding: 8px 16px;
  cursor: pointer;
  transition: all var(--transition-fast);
  font-size: 14px;
  outline: none;
}

button:hover {
  background-color: rgba(100, 255, 218, 0.1);
  box-shadow: 0 0 10px rgba(100, 255, 218, 0.3);
}

button:active {
  transform: translateY(1px);
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--medium-bg);
}

::-webkit-scrollbar-thumb {
  background: var(--light-bg);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--accent-color);
}

/* Utility classes */
.text-accent {
  color: var(--accent-color);
}

.text-secondary {
  color: var(--secondary-accent);
}

.text-danger {
  color: var(--danger-color);
}

.text-success {
  color: var(--success-color);
}

.text-warning {
  color: var(--warning-color);
}

.text-center {
  text-align: center;
}

.loading-spinner {
  border: 4px solid rgba(255, 255, 255, 0.1);
  border-top: 4px solid var(--accent-color);
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  h1 {
    font-size: 2rem;
  }
  
  h2 {
    font-size: 1.6rem;
  }
  
  h3 {
    font-size: 1.3rem;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 1.8rem;
  }
  
  h2 {
    font-size: 1.4rem;
  }
  
  h3 {
    font-size: 1.2rem;
  }
  
  button {
    padding: 6px 12px;
    font-size: 13px;
  }
}
