.controls-section {
  background-color: var(--medium-bg);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 30px;
  box-shadow: var(--card-shadow);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.controls-title {
  margin-bottom: 20px;
  color: var(--accent-color);
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.controls-title button {
  background: none;
  border: none;
  color: var(--text-medium);
  cursor: pointer;
  font-size: 1.2rem;
  padding: 0;
  transition: color var(--transition-fast);
}

.controls-title button:hover {
  color: var(--text-light);
}

.controls-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.control-group {
  margin-bottom: 15px;
}

.control-label {
  display: block;
  margin-bottom: 8px;
  color: var(--text-medium);
  font-size: 0.9rem;
}

.control-input {
  width: 100%;
  background-color: var(--light-bg);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  padding: 8px 12px;
  color: var(--text-light);
  font-size: 14px;
}

.control-input:focus {
  outline: none;
  border-color: var(--accent-color);
  box-shadow: 0 0 0 2px rgba(100, 255, 218, 0.2);
}

.range-slider {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.range-slider-input {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 6px;
  border-radius: 3px;
  background: var(--light-bg);
  outline: none;
  margin: 10px 0;
}

.range-slider-input::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: var(--accent-color);
  cursor: pointer;
  transition: all var(--transition-fast);
}

.range-slider-input::-moz-range-thumb {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: var(--accent-color);
  cursor: pointer;
  transition: all var(--transition-fast);
}

.range-slider-input::-webkit-slider-thumb:hover {
  transform: scale(1.2);
  box-shadow: 0 0 10px rgba(100, 255, 218, 0.5);
}

.range-slider-input::-moz-range-thumb:hover {
  transform: scale(1.2);
  box-shadow: 0 0 10px rgba(100, 255, 218, 0.5);
}

.range-values {
  display: flex;
  justify-content: space-between;
  color: var(--text-medium);
  font-size: 0.8rem;
}

.multi-select {
  width: 100%;
  background-color: var(--light-bg);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  color: var(--text-light);
}

.multi-select__control {
  background-color: var(--light-bg) !important;
  border-color: rgba(255, 255, 255, 0.1) !important;
  box-shadow: none !important;
}

.multi-select__control--is-focused {
  border-color: var(--accent-color) !important;
  box-shadow: 0 0 0 1px var(--accent-color) !important;
}

.multi-select__menu {
  background-color: var(--medium-bg) !important;
  border: 1px solid var(--light-bg) !important;
  box-shadow: var(--card-shadow) !important;
}

.multi-select__option {
  background-color: var(--medium-bg) !important;
  color: var(--text-light) !important;
}

.multi-select__option--is-focused {
  background-color: rgba(100, 255, 218, 0.1) !important;
}

.multi-select__option--is-selected {
  background-color: rgba(100, 255, 218, 0.2) !important;
  color: var(--accent-color) !important;
}

.multi-select__multi-value {
  background-color: rgba(100, 255, 218, 0.15) !important;
  border-radius: 4px !important;
}

.multi-select__multi-value__label {
  color: var(--text-light) !important;
}

.multi-select__multi-value__remove:hover {
  background-color: rgba(255, 77, 77, 0.2) !important;
  color: var(--danger-color) !important;
}

.toggle-switches {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.toggle-switch {
  display: flex;
  align-items: center;
}

.toggle-label {
  display: flex;
  align-items: center;
  cursor: pointer;
  color: var(--text-medium);
  font-size: 0.9rem;
}

.toggle-input {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-slider {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
  background-color: var(--light-bg);
  border-radius: 20px;
  margin-right: 10px;
  transition: all var(--transition-fast);
}

.toggle-slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 2px;
  bottom: 2px;
  background-color: var(--text-dark);
  border-radius: 50%;
  transition: all var(--transition-fast);
}

.toggle-input:checked + .toggle-slider {
  background-color: rgba(100, 255, 218, 0.3);
}

.toggle-input:checked + .toggle-slider:before {
  transform: translateX(20px);
  background-color: var(--accent-color);
}

.toggle-icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-right: 6px;
}

.toggle-icon.military {
  background-color: var(--military-color);
}

.toggle-icon.nuclear {
  background-color: var(--nuclear-color);
}

.view-mode-buttons {
  display: flex;
  gap: 10px;
}

.view-mode-button {
  background-color: var(--light-bg);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  padding: 8px 12px;
  color: var(--text-medium);
  font-size: 0.9rem;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.view-mode-button.active {
  background-color: rgba(100, 255, 218, 0.1);
  color: var(--accent-color);
  border-color: var(--accent-color);
}

.view-mode-button:hover {
  background-color: rgba(100, 255, 218, 0.05);
  color: var(--text-light);
}

.optimization-select {
  display: flex;
  gap: 10px;
  align-items: center;
}

.optimization-option {
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all var(--transition-fast);
  background-color: var(--light-bg);
  color: var(--text-medium);
}

.optimization-option.active {
  background-color: rgba(100, 255, 218, 0.1);
  color: var(--accent-color);
}

.optimization-option:hover {
  background-color: rgba(100, 255, 218, 0.05);
  color: var(--text-light);
}

@media (max-width: 768px) {
  .controls-grid {
    grid-template-columns: 1fr;
  }
  
  .toggle-switches {
    flex-direction: column;
    gap: 10px;
  }
}
